$cell-length: 42px;

@import "reset";
@import "ships_and_cells";

// div {
//     border: 1px solid red;
// }

body {
	font-family: Arial, Helvetica, Ubuntu, sans-serif;
	height: 100vh;
	width: 100%;
	background-color: rgb(223, 238, 252);
}

button {
	height: 2rem;
	padding: 0.5rem;
	box-sizing: border-box;
	width: 90%;
	background-color: rgb(108, 176, 240);
	border: 0px;
	border-radius: 100px;
	font-weight: bold;
	outline-style: none;
}

.boards > * {
	max-width: $cell-length * 10;
	margin: 0 1rem;
}

.boards {
	width: 100%;
	justify-content: space-around;
	padding-top: 1rem;
	box-sizing: border-box;
	display: flex;
}

.board-header {
	font-size: 2rem;
	text-align: center;
	margin: 0.25rem;
}

.board {
	width: $cell-length * 10;
	height: $cell-length * 10;
	display: flex;
	flex-wrap: wrap;
}

.entrant-pieces {
	padding-top: 1rem;
	justify-content: space-between;
	box-sizing: border-box;
	width: 100%;
	flex: 1;
	height: $cell-length * 5;
	display: flex;
	align-items: flex-start;
	flex-wrap: wrap;
}

.piece-heading {
	text-align: center;
}

.parent-announcements {
	flex: 1 1 200px;
	margin: 1rem;
}

.announcements {
	padding: 0.5rem;
	margin-top: 1.5rem;
	box-sizing: border-box;
	opacity: 1;
	background-color: rgb(211, 234, 255);
	border: 2px solid rgb(153, 178, 207);
	border-radius: 10px;
}

.announcements > * {
	font-size: 1.25rem;
	line-height: 1.5;
}

@media (max-width: 1200px) {
	.announcements > * {
		font-size: 1rem;
		line-height: 1.25;
	}
}

@media (max-width: 979px) {
	.announcements > * {
		font-size: 0.75rem;
		line-height: 1.25;
	}
}

.commands {
	margin-top: 1rem;
	height: 200px;
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	align-items: center;
	background-color: rgb(211, 234, 255);
	border: 2px solid rgb(153, 178, 207);
	border-radius: 10px;
}

.unselectable {
	-moz-user-select: none;
	-khtml-user-select: none;
	-webkit-user-select: none;
	-ms-user-select: none;
	user-select: none;
}
